const stops = [
	{name: '', value: -1, checked: true},
	{name: ' ', value: 0, checked: true},
	{name: '1 ', value: 1, checked: true},
	{name: '2 ', value: 2, checked: true},
	{name: '3 ', value: 3, checked: true}
];

export default function(state = stops, action) {
	if(action.type === 'STOPS_UPDATED') {
		return [].concat(action.payload.stops);
	}

	return state;
};



